'use client'
import type { Metadata } from "next";
import localFont from "next/font/local";
import "./globals.css";
import {Provider} from '@/components/ui/provider'
import Nav from "@/components/Nav";
import DashBar from "@/components/DashBar";
import MobileBar from "@/components/MobileBar";
import { AuthProvider } from "./contexts/authProvider";
import { ChakraProvider } from "@chakra-ui/react";
import { usePathname } from "next/navigation";
// import { extendTheme } from "@chakra-ui/theme-tools";




const geistSans = localFont({
  src: "./fonts/GeistVF.woff",
  variable: "--font-geist-sans",
  weight: "100 900",
});
const geistMono = localFont({
  src: "./fonts/GeistMonoVF.woff",
  variable: "--font-geist-mono",
  weight: "100 900",
});

export const metadata: Metadata = {
  title: "Create Next App",
  description: "Generated by create next app",
};



export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const pathname = usePathname();

  // Routes où on ne veut PAS afficher navbar/sidebar
  const authRoutes = ["/auth/login", "/auth/register"];
  const isAuthPage = authRoutes.includes(pathname);
  return (
    <html lang="en" suppressHydrationWarning>
      <body
        className={`${geistSans.variable} ${geistMono.variable} antialiased bg-[#0e0e0e] text-[#ececec]`}
      >
          {/* ChakraProvider avec un thème personnalisé */}
          
        <AuthProvider>
        <Provider>
          <MobileBar />
          <Nav /> 
          
          <div className="flex items-start">
            <div>
              <DashBar />
              <MobileBar />
            </div>
            {children}
          </div>
          
        </Provider>
        </AuthProvider>
        
      </body>
    </html>
  );
}
